import { ReportServiceClient } from './reportServiceClient';

export * from './reportServiceClient';

export type ServiceClientClasses = ReportServiceClient;

export type FunctionInClass<T> = {
    // eslint-disable-next-line @typescript-eslint/ban-types
    [K in keyof T]: T[K] extends Function ? T[K] : never;
}[keyof T];

export type NoParamFunctionInClass<T> = {
    [K in keyof T]: T[K] extends (p1?: AbortSignal | undefined) => any ? T[K] : never;
}[keyof T];

export type PagedFunctionInClass<T> = {
    [K in keyof T]: T[K] extends ({}: any) => Promise<{ results: any[]; skipToken?: string }> ? T[K] : never;
}[keyof T];

export type APIFunctions = FunctionInClass<ServiceClientClasses>;
