import type { AxiosResponse } from 'axios';
export const CLIENT_ERROR_PRIX = 'C_ERR_';
/**
 * all code starts with `C_ERR` @see CLIENT_ERROR_PRIX
 * meaning `Client defined ERRor `
 */
export enum ClientErrorCodes {
    unknown = 'C_ERR_UNKNOWN',
    /**
     * uncatched server down exception, user can try later
     */
    unknownServerInternal = 'C_ERR_UNKNOWN_SERVER_INTERNAL',
    /**
     * uncatched Error for paths or AFD routing
     */
    unknownServerRequest = 'C_ERR_UNKNOWN_SERVER_REQUEST',
    /**
     * uncatched
     */
    unknownRuntime = 'C_ERR_UNKNOWN_RUNTIME',
    /**
     * Network Connection Error
     */
    network = 'C_ERR_NETWORK',
    /**
     * Teams SDK Errors
     */
    teamsSDK = 'C_ERR_TEAMS_SDK',
    /**
     * JS Runtime error
     */
    runtime = 'C_ERR_RUNTIME',
}

export const enum ErrorCategory {}

/**
 * RequstError
 * - parse normal server error body {Code:string,Message:string}
 * - parse unknown server error
 * - warpper common error
 */
export class RequestError extends Error implements DetailedError {
    name = 'RequestError';
    /**
     * Unique Error Code String
     *
     * @todo repalce `string` with server code type
     */
    code: ClientErrorCodes | string;

    /**
     * can user retry it or not
     */
    isRetriable: boolean;
    /**
     * Status Code
     */
    status?: number;
    /**
     * response headers
     */
    headers: { [key: string]: any } = {};

    /**
     * full response data
     */
    response: { Code: string; category: string; Message: string } | string | undefined;

    /**
     * Client Request Id
     */
    requestId?: string;

    /**
     * additional Error Message
     */
    additionalErrorMessage?: AdditionalErrorMessage;

    /**
     * detailed Errors
     */
    detailedErrors?: DetailedError[];

    constructor(res: AxiosResponse | Error, code?: ClientErrorCodes, requestId?: string) {
        super();

        this.requestId = requestId;
        if (res instanceof Error) {
            this.code = code || ClientErrorCodes.unknown;
            this.message = `Request Erorr: ${res.message}`;
            this.isRetriable = this.code !== ClientErrorCodes.runtime && this.code !== ClientErrorCodes.unknownRuntime;
            this.stack = res.stack;
        } else if (res.status) {
            // AxiosResponse
            const data = res.data;
            if (data?.Code) {
                // Server Cached error
                this.code = data.Code;
            } else if (res.status >= 500) {
                this.code = ClientErrorCodes.unknownServerInternal;
            } else if (res.status >= 400) {
                this.code = ClientErrorCodes.unknownServerRequest;
            } else {
                this.code = ClientErrorCodes.unknownServerRequest;
            }

            this.message =
                typeof data === 'string'
                    ? data
                    : data?.Message || res.statusText || 'An unexpected server error occurred';
            this.status = res.status;
            this.headers = res.headers;
            this.response = data;
            // isRetriable in response body first, otherwise using status code
            this.isRetriable = data?.IsRetriable ?? res.status >= 500;
            // eslint-disable-next-line @typescript-eslint/no-unsafe-call
            this.additionalErrorMessage = data?.AdditionalMessage?.map(
                (item: {
                    UserNames: string[];
                    UserIds: string[];
                    EntityNames: string[];
                    EntityIds: string[];
                    HelpLink: string;
                }) => {
                    return convertAdditionMessage(item);
                }
            );
            // eslint-disable-next-line @typescript-eslint/no-unsafe-call
            this.detailedErrors = data?.DetailedErrors?.map(
                (item: { Code: string; Message: string; IsRetriable: boolean; AdditionalMessage: [] }) => {
                    return {
                        code: item.Code,
                        message: item.Message,
                        isRetriable: item.IsRetriable,
                        additionalErrorMessage: convertAdditionMessage(item.AdditionalMessage),
                    };
                }
            );
        } else {
            // others data
            this.code = code || ClientErrorCodes.unknown;
            this.message = `${(res as any as Error)?.message || res}` || 'An unexpected server error occurred';
            this.isRetriable = true;
        }
    }
}

function convertAdditionMessage(additionalMessage: any): AdditionalErrorMessage {
    return {
        userNames: additionalMessage.UserNames,
        userIds: additionalMessage.UserIds,
        entityNames: additionalMessage.EntityNames,
        entityIds: additionalMessage.EntityIds,
        helpLink: additionalMessage.HelpLink,
    };
}

export interface DetailedError {
    code: string;
    message?: string;
    isRetriable: boolean;
    additionalErrorMessage?: AdditionalErrorMessage;
}

export interface AdditionalErrorMessage {
    userNames?: string[];
    userIds?: string[];
    entityNames?: string[];
    entityIds?: string[];
    helpLink?: string;
}
