import { axiosInstance } from './axios';
import type { ServiceClientClasses } from './gen';
import * as ServiceClientsClassMap from './gen';

/**
 * Clients For all REST API Call
 *
 * @ref ServiceClientClasses
 */
export const SERVICE_CLIENT: ServiceClientClasses = {} as ServiceClientClasses;
Object.entries(ServiceClientsClassMap)
    .filter(([key]: [string, any]): boolean => key.endsWith('ServiceClient'))
    .forEach(([_, ClientClass]: [string, any]): void => {
        // eslint-disable-next-line @typescript-eslint/no-unsafe-call
        const client = new ClientClass('', axiosInstance);
        Object.getOwnPropertyNames(ClientClass.prototype).forEach(
            (funcName: keyof ServiceClientClasses | 'constructor' | string): void => {
                if (funcName !== 'constructor') {
                    // eslint-disable-next-line @typescript-eslint/no-unsafe-call
                    SERVICE_CLIENT[funcName] = client[funcName].bind(client);
                }
            }
        );
    });
