import { InfiniteData, QueryClient } from 'react-query';
import { queryClient as defaultQueryClient } from '../service/queryClient';
import { PagedFunctionInClass, ServiceClientClasses } from './gen';

/**
 * update page query cache.
 *
 * @param queryClient query client
 * @param funcs paged SERVICE_CLIENT.method array - service client API function @see SERVICE_CLIENT
 * @param id id to search, if undefined return the first query cache
 * @param update cache update function
 */
export function updateReactPageQueryCache<T extends PagedFunctionInClass<ServiceClientClasses>>(
    funcs: T[],
    id: string,
    update: (item: Awaited<ReturnType<T>>['results'][number]) => Awaited<ReturnType<T>>['results'][number] | undefined,
    queryClient: QueryClient = defaultQueryClient
): void {
    const funcNames = funcs.map(f => f.name);
    const dataList = queryClient.getQueriesData<InfiniteData<Awaited<ReturnType<T>>>>({
        predicate: query => funcNames.some(k => query.queryKey.includes(k)),
    });

    for (const queryData of dataList.reverse()) {
        if (queryData[1]?.pages) {
            for (const [pageIndex, page] of queryData[1].pages.entries()) {
                // eslint-disable-next-line @typescript-eslint/no-unsafe-call
                const itemIndex = page.results?.findIndex(i => i?.id === id);
                if (itemIndex >= 0) {
                    const [key, data] = queryData;
                    const items = data.pages[pageIndex].results;
                    const newItem = update(items[itemIndex]);
                    items.splice(itemIndex, 1, newItem as any);
                    queryClient.setQueryData(key, { ...data });
                }
            }
        }
    }
}
