import { IHttpInfo } from '@addressbook/components-logger';
import { getAuthToken, getTeamsContext, TeamsError } from '@addressbook/components-metaos-host';
import { uuid } from '@addressbook/utilities-core';
import Axios, {
    AxiosError,
    AxiosHeaders,
    AxiosResponse,
    AxiosResponseHeaders,
    InternalAxiosRequestConfig,
    RawAxiosResponseHeaders,
} from 'axios';
import { HEADER_CLIENT_REQUEST_ID, HEADER_RESPONSE_CORRELATION_ID, HEADER_ROUTE_KEY } from '../common/Constants';
import { ADDRESSBOOK_APP_RESOURCE_URL } from '../common/env';
import { LogCategory, logger } from '../logger';
import { tryParseAfdRefFromHeaders } from '../utilities/Utils';
import { ClientErrorCodes, RequestError } from './RequestError';

export interface ExtAxiosConfig {
    _startRequestTime?: number;
    _getTeamsContextTime?: number;
    _getAuthTokenTime?: number;
}

/**
 * check statusCode is validate (2xx)
 *
 * @param status
 * @returns
 */
const isValidateStatus = (status: number): boolean => status >= 200 && status < 300;

export const axiosInstance = Axios.create({
    validateStatus: isValidateStatus,
    paramsSerializer: {
        serialize: params =>
            new URLSearchParams(
                Object.entries(params).filter((v): v is [string, string] => v[1] !== undefined)
            ).toString(),
    },
});

axiosInstance.interceptors.request.use(transformRequest);
axiosInstance.interceptors.response.use(transformResponse, transformError);

async function transformRequest(
    config: InternalAxiosRequestConfig & ExtAxiosConfig
): Promise<InternalAxiosRequestConfig> {
    if (!config.headers) {
        config.headers = new AxiosHeaders({
            [HEADER_CLIENT_REQUEST_ID]: uuid(),
        });
    } else if (!config.headers[HEADER_CLIENT_REQUEST_ID]) {
        config.headers[HEADER_CLIENT_REQUEST_ID] = uuid();
    }
    logger.logTraceDebug(LogCategory.APIRequest, `Calling getAuthToken for ${ADDRESSBOOK_APP_RESOURCE_URL}`);

    config._getTeamsContextTime = performance.now();
    const context = await getTeamsContext();
    config._getAuthTokenTime = performance.now();
    config.headers.authorization = `Bearer ${await getAuthToken(ADDRESSBOOK_APP_RESOURCE_URL)}`;
    config.headers[HEADER_ROUTE_KEY] = context.user?.tenant?.id;
    config._startRequestTime = performance.now();
    return config;
}

function getRequestId(config?: InternalAxiosRequestConfig): string | undefined {
    return config?.headers?.[HEADER_CLIENT_REQUEST_ID] as string;
}
function getHttpInfo(
    config: InternalAxiosRequestConfig,
    responseHeaders?: RawAxiosResponseHeaders | AxiosResponseHeaders
): IHttpInfo {
    const url = config.url || '';
    const getTeamsContextTime = (config as ExtAxiosConfig)?._getTeamsContextTime || performance.now();
    const getAuthTokenTime = (config as ExtAxiosConfig)?._getAuthTokenTime || performance.now();
    const startRequestTime = (config as ExtAxiosConfig)?._startRequestTime || performance.now();
    const getTeamsContextLatency = getAuthTokenTime - getTeamsContextTime;
    const getAuthTokenLatency = startRequestTime - getAuthTokenTime;
    const latency = performance.now() - startRequestTime;
    const clientRequestId = getRequestId(config)!;
    const correlationId = responseHeaders?.[HEADER_RESPONSE_CORRELATION_ID] as string;
    const edgeRef = tryParseAfdRefFromHeaders(responseHeaders);
    return {
        url,
        getTeamsContextLatency,
        getAuthTokenLatency,
        latency,
        clientRequestId,
        correlationId,
        apiName: new URL(url, config.baseURL || location.origin).pathname,
        requestMethod: config.method || 'GET',
        edgeRef,
    };
}
export function transformResponse<T>(response: AxiosResponse<T>): T {
    const { status = 0, config } = response;
    const httpInfo = getHttpInfo(config, response.headers);
    if (!isValidateStatus(status)) {
        // log http exception
        logger.logTraceHttpException(
            httpInfo.apiName || '',
            (response.data as any)?.Message || response.statusText,
            status
        );
    } else {
        logger.logTraceDebug(LogCategory.APIRequest, `success for ${response.config.url}`);
    }
    const httpRequest: IHttpInfo = {
        ...httpInfo,
        responseCode: status,
        responseText: response.statusText,
    };

    logger.logHttp(httpRequest);
    return response.data;
}

async function transformError(error: AxiosError): Promise<never> {
    if (error?.code === AxiosError.ERR_NETWORK) {
        logger.logTraceError(LogCategory.NetworkError, `NetworkError ${error?.config?.url} ${error.message}`);
        if (error.config) {
            logger.logHttp(getHttpInfo(error.config));
        }
        throw new RequestError(error, ClientErrorCodes.network, getRequestId(error.config));
    } else if (error?.response) {
        // get response from server will log a http error
        const response = error.response;

        if (response?.data instanceof Blob && response?.data.type === 'application/json') {
            try {
                const errorString = JSON.parse(await response?.data.text());
                response.data = errorString;
            } catch (e) {
                logger.logTraceError(LogCategory.APIRequest, `Invalid JSON response from Blob: ${response?.data}`);
            }
        }
        transformResponse(response);
        throw new RequestError(response, undefined, getRequestId(error.config));
    } else if (Axios.isCancel(error) as any) {
        logger.logTraceInfo(LogCategory.APIRequest, `Cancel ${error?.config?.url}`);
    } else if (error?.config) {
        // no response
        // usually network connection error
        const logCategory = getAxiosErrorLogCategory(error?.code);
        logger.logTraceError(logCategory, `NetworkError ${error?.config?.url} ${error.message}`);
        logger.logHttp(getHttpInfo(error.config));
        throw new RequestError(error, ClientErrorCodes.network, getRequestId(error.config));
    } else if (error instanceof Error) {
        const isTeamsSDKError = error instanceof TeamsError;
        // runtime error
        logger.logTraceException(isTeamsSDKError ? LogCategory.TeamsSDKError : LogCategory.RuntimeError, {
            error,
            message: `${error.name} ${error?.config?.url}`,
        });
        throw new RequestError(error, isTeamsSDKError ? ClientErrorCodes.teamsSDK : ClientErrorCodes.runtime);
    } else {
        logger.logTraceError(LogCategory.UnknownRequestError, `UnknownError ${error}`);
    }

    throw new RequestError(error);
}

// https://github.com/axios/axios/blob/main/lib/adapters/xhr.js
function getAxiosErrorLogCategory(errorCode?: string): LogCategory {
    switch (errorCode) {
        case AxiosError.ERR_NETWORK:
            return LogCategory.NetworkError;
        case AxiosError.ETIMEDOUT:
            return LogCategory.TimeoutExceeded;
        case AxiosError.ECONNABORTED:
            return LogCategory.RequestAborted;
        default:
            return LogCategory.APIRequest;
    }
}
