import { onlineManager, QueryClient, setLogger } from 'react-query';
import { createWebStoragePersistor } from 'react-query/createWebStoragePersistor-experimental';
import { persistQueryClient } from 'react-query/persistQueryClient-experimental';
import { initQuery, isDev, URIParam } from '../common/env';
import { LogCategory, logger } from '../logger';
import { RequestError } from './RequestError';

/**
 * the global query client
 * https://react-query.tanstack.com/reference/useQuery#_top
 */
export const queryClient = /* #__PURE__ */ new QueryClient({
    defaultOptions: {
        queries: {
            cacheTime: 24 * 60 * 60 * 1000, // 24 hours
            staleTime: 1 * 5 * 60 * 1000, // 5 minute for data stale
            refetchInterval: false, // auto refresh every 5 mins
            refetchIntervalInBackground: false, // don't refresh in background
            refetchOnWindowFocus: false,
            retryDelay(retry: number) {
                const delayTime = 1500;
                return retry * delayTime;
            },
            retry: (failureCount, error) => {
                // Need explicit error type, because error defaults to unknown type and our error is always the specific type.
                // https://github.com/TanStack/query/discussions/2757
                if (error instanceof Error) {
                    const genericError = error as RequestError;
                    return (
                        failureCount < 3 &&
                        genericError.status !== 404 &&
                        genericError.status !== 429 &&
                        genericError?.status !== 403
                    );
                }
                return false; // No auto-retry
            },
        },
    },
});

const userObjectId = initQuery.get(URIParam.UserId)!;

const key = 'REACT_QUERY_OFFLINE_CACHE';
const persistor = createWebStoragePersistor({ storage: window.localStorage, key });
export const clearQueryClientPersistor = (): void => window.localStorage.removeItem(key);

persistQueryClient({
    queryClient,
    persistor,
    maxAge: 7 * 24 * 60 * 60 * 1000,
    buster: (userObjectId ? 'APICache-' + userObjectId : 'CommonAPICache') + '-V3',
});

// Always set to react query online state to true, to avoid pausing request sending and error handling.
window.ononline = () => {
    onlineManager.setOnline(true);
};

window.onoffline = () => {
    onlineManager.setOnline(true);
};

setLogger(
    isDev
        ? console
        : {
              log: args => {
                  logger.logTraceInfo(LogCategory.ReactQuery, JSON.stringify(args));
              },
              warn: args => {
                  logger.logTraceWarning(LogCategory.ReactQuery, JSON.stringify(args));
              },
              error: args => {
                  logger.logTraceError(LogCategory.ReactQuery, JSON.stringify(args));
              },
          }
);
